<template>
    <div class='batchFill'>
        <el-steps :active="2" finish-status="success" simple style="margin-top: 20px">
            <el-step title="步骤 1" ></el-step>
            <el-step title="步骤 2" ></el-step>
            <el-step title="步骤 3" ></el-step>
        </el-steps>
        <el-container>
            <el-header>
                <el-row>
                    <el-col :span="4"><h3>您的真实高考成绩</h3></el-col>
                    <el-col :span="16">
                        <ul class="scoreUl">
                            <li>
                                <label>科目:</label>
                                <div class="scoreLiDiv">{{scoreData.subject}}</div>
                            </li>
                            <li>
                                <label>总分:</label>
                                <div class="scoreLiDiv">{{scoreData.score}}</div>
                            </li>
                            <li>
                                <label>位次:</label>
                                <div class="scoreLiDiv">{{scoreData.position}}</div>
                            </li>
                        </ul>
                    </el-col>
                </el-row>
            </el-header>
            <el-main>
                <ul class="batchUl">

                </ul>
            </el-main>
        </el-container>
    </div>
</template>

<script>
export default {
  name: "batchFill",
  data() {
    return {
      scoreData:{}
    };
  },
  methods: {
   

  },
  mounted: function() {
      const _that = this;
      
    //   {{$store.scoreData.score}}
    console.log(_that.$store.scoreData)
    if(_that.$store.scoreData){
        _that.scoreData = _that.$store.scoreData; 
    }else{
        _that.$router.push('/recordEntry');
    }
  }
};
</script>

<style lang="less">
    .batchFill{
        width: 80%;
        height: 100%;
        overflow-y: auto;
        margin: 0 auto;
        .el-steps{
            margin-top: 0!important;
        }
        .el-header{
            background: #f2f0f0;
            height: 50px!important;
            h3{
                min-width: 240px;
                padding: 10px 0;
                margin: 0;
                line-height: 30px;
            }
            .scoreUl{
                list-style: none;
                height: 30px;
                line-height: 30px;
                width: 100%;
                padding: 10px 0;
                margin: 0;
                li{
                    float: left;
                    width: 100px;
                    height: 30px;
                    line-height: 30px;
                    label{
                        width: 40px;
                        height: 30px;
                        line-height: 30px;
                        float: left;
                        font-size: 14px;
                    }
                    .scoreLiDiv{
                        text-align: left;
                        color: #FC8505;
                        font-weight: 600;
                        margin-left: 44px;
                        font-size: 15px;
                        height: 30px;
                        line-height: 30px;
                    }
                }
            }
        }
        .el-main{
            margin-top: 10px;
            background: #fff;
        }
    }
   
</style>



